import React, { useEffect } from 'react';
import { useNavigate, useLocation } from 'react-router-dom';

const LoginPage = () => {
  const navigate = useNavigate();
  const location = useLocation();

  useEffect(() => {
    // Получаем токен из URL: ?token=abc123
    const searchParams = new URLSearchParams(location.search);
    const token = searchParams.get('token');

    if (!token) {
      alert('Токен не указан в ссылке');
      navigate('/');
      return;
    }

    // Загружаем tokens.json
    fetch('../public/tokens.json')
      .then(res => {
        if (!res.ok) {
          throw new Error('Не удалось загрузить файл tokens.json');
        }
        return res.json();
      })
      .then(tokens => {
        // Ищем совпадение по токену
        const found = tokens.find(item => item.token === token);

        if (found) {
          // Сохраняем telegram_id в localStorage
          localStorage.setItem('user', JSON.stringify({ telegramId: found.telegram_id }));
          navigate('/profile'); // или '/', '/dashboard' — куда угодно
        } else {
          alert('Неверный или устаревший токен');
          navigate('/');
        }
      })
      .catch(err => {
        console.error('Ошибка при обработке токена:', err);
        alert('Не удалось проверить токен. Попробуйте позже.');
        navigate('/');
      });
  }, [navigate, location]);

  return (
    <div className="login-page">
      <h2>Авторизация</h2>
      <p>Чтобы войти, отправьте боту команду /start:</p>
      <a href="https://t.me/runwayshopbot">Начать  чат с ботом</a>
    </div>
  );
};

export default LoginPage;